@media (max-width: 767px) {

  .ourteam-block {
    padding: 35px 20px 35px;

    &__list {
      @include centering_in_column;
      list-style: none;
    }

    &__item {
      max-width: 280px;
      text-align: start;
    }

    &__item:not(:first-child) {
      margin-top: 49px;
    }

    &__image {
      width: 280px;
      height: 227.25px;
    }

    &__image > img {
      width: 100%;
      height: 100%;
    }

    &__header {
      margin-top: 28px;

      font-weight: 700;
      font-size: 18px;
      line-height: 21px;
      text-transform: capitalize;
    }

    &__subheader {
      margin-top: 6px;

      font-weight: 400;
      font-size: 14px;
      line-height: 16px;
      text-transform: uppercase;

      color: #FF4343;
    }

    &__text {
      margin-top: 39px;

      font-weight: 400;
      font-size: 14px;
      line-height: 16px;

      color: #6C7279;
    }

    &__links {
      margin-top: 55px;

      display: flex;
      column-gap: 6px;

      list-style: none;
    }

    &__link {
      width: 39.61px;
      height: 39.61px;

      transition-duration: 0.4s;
    }

    &__link:hover{
      background-color:#6c7279;
    }

    &__link a img {
      width: 100%;
      height: 100%;
    }
  }
}

