@media (min-width: 768px) and (max-width: 1024px) {
  .header-block {
    max-width: 984px;
    border-bottom: 1px solid #babbc5;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    display: flex;
  }

  .header-block__link {
    padding: 38px 0 32px;
  }

  .header-block__nav {
    cursor: pointer;
  }

  .header-block__hamburger {
    display: none;
  }

  .header-block__links {
    flex-wrap: wrap;
    align-items: center;
    column-gap: 29px;
    margin-right: 11px;
    list-style: none;
    display: flex;
  }

  .header-block__item {
    text-transform: uppercase;
    color: #fff;
    padding: 41px 9px;
    font-size: 12px;
    font-weight: 700;
    line-height: 14px;
    text-decoration: none;
    transition-duration: .4s;
    position: relative;
  }

  .header-block__item:hover:after {
    content: "";
    width: 100%;
    height: 3px;
    background-color: #babbc5;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .header-block__item:active:after {
    content: "";
    width: 100%;
    height: 3px;
    background-color: #e71414;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .header-block__item:active {
    color: #e71414;
    transition-duration: .4s;
  }
}

