@media (max-width: 767px) {

  .testimonials-block {

    &__image {
      width: 320px;
      height: 212px;
      margin: 0 auto;

    }

    &__image > img {
      width: 100%;
      height: 100%;
    }

    &__quote {
      max-width: 320px;
      margin: 0 auto;
      padding: 24px 0 100px;

      background-color: #5C3249;
    }

    &__text {
      width: 89%;
      margin: 0 0 26px 15px;

      font-family: 'Cardo', serif;
      font-style: italic;
      font-weight: 400;
      font-size: 24px;
      line-height: 32px;
      text-transform: capitalize;

      color: #FFFFFF;
    }

    &__author {
      margin-right: 17px;

      font-weight: 700;
      font-size: 12px;
      line-height: 14px;
      text-transform: uppercase;

      color: #FFFFFF;
    }

    &__slider {
      margin-top: 23px;

      display: flex;
      justify-content: center;
      column-gap: 20px;

      cursor: pointer;
    }

  }
}

@media (min-width: 768px) and (max-width: 1024px) {

  .testimonials-block {

    &__image {
      width:502px;
      height: 332px;
    }

    &__image > img {
      width: 100%;
      height: 100%;
    }
    &__info{
      display: flex;

    }

    &__quote {
      width: 51%;

      display: flex;
      flex-direction: column;


      background-color: #5C3249;
    }

    &__quote > blockquote{
      margin: 62px 0 0 88px;
    }

    &__text {
      width: 70%;

      font-family: 'Cardo', serif;
      font-style: italic;
      font-weight: 400;
      font-size: 24px;
      line-height: 32px;
      text-transform: capitalize;
      text-align: start;

      color: #FFFFFF;
    }

    &__author {
      width:84%;
      margin: 16px 0 0 0;

      font-weight: 700;
      font-size: 12px;
      line-height: 14px;
      text-transform: uppercase;

      color: #FFFFFF;
    }

    &__slider {
      margin-top: 23px;

      display: flex;
      justify-content: center;
      column-gap: 25px;

      cursor: pointer;
    }
  }
}


@media (min-width: 1025px) {

  .testimonials-block {

    &__image {
      width: 741px;
      height: 490px;
    }

    &__image > img {
      width: 100%;
      height: 100%;
    }
    &__info{
      display: flex;

    }

    &__quote {
      width: 51%;

      display: flex;
      flex-direction: column;

      background-color: #5C3249;
    }

    &__quote > blockquote{
      margin: 133px 0 0 128px;
    }

    &__text {
      width: 71%;

      font-family: 'Cardo', serif;
      font-style: italic;
      font-weight: 400;
      font-size: 36px;
      line-height: 49px;
      text-transform: capitalize;
      text-align: start;

      color: #FFFFFF;
    }

    &__author {
      width: 70%;
      margin: 24px 0 0 0;

      font-weight: 700;
      font-size: 13px;
      line-height: 15px;
      text-transform: uppercase;

      color: #FFFFFF;
    }

    &__slider {
      column-gap: 29px;
      margin-top: 33px;

      display: flex;
      justify-content: center;

      cursor: pointer;
    }
  }
}
